          <button @click="activeTab = 'sheet'" :disabled="activeTab === 'sheet'" class="btn btn-light-success font-weight-bolder font-size-sm">Tenaga Kerja</button>
          <button @click="activeTab = 'jurnal'" :disabled="activeTab === 'jurnal'" class="btn btn-light-success font-weight-bolder font-size-sm ml-3">Jabatan</button>
          <button @click="activeTab = 'fina'" :disabled="activeTab === 'fina'" class="btn btn-light-danger font-weight-bolder font-size-sm ml-3">Jurnal Keuangan</button>
        </div> -->
        <div class="card-toolbar">
          <ul class="nav nav-tabs nav-tabs-line" role="tablist">
