                  </template>
                </tr>
              </thead>
              <tbody>
                <template v-for="(d,i) in mainData[activeTab]">
                  <tr :key="i+'-data-'+activeTab">
                    <td class="nowrap-table text-right">{{ i+1 }}.</td>
                    <td @click="getPayrollDetail('modal-payroll-details', d)" class="sticky-left font-weight-bolder text-primary pointer bg-hover-light-primary" :class="fullwidthColumns.includes('fullname') ? 'nowrap-table': 'max-w-170px'">
                      <div class="text-line-1"> {{ d.fullname }} </div>
                    </td>
                    <td @click="setFullWidth('rel_job_position_category_id')" class="pointer" :class="fullwidthColumns.includes('rel_job_position_category_id') ? 'nowrap-table': ''">
                      <div class="text-line-1">{{ d.rel_job_position_category_id }} </div>
                    </td>
                    <td @click="setFullWidth('rel_job_position_id')" class="pointer" :class="fullwidthColumns.includes('rel_job_position_id') ? 'nowrap-table': ''">
