                          <span class="">Rp.</span>
                          <span class="text-right">{{ s.value | parse('number2fraction') }}</span>
                        </div>
                        <span v-else class="d-block text-right">-</span>
                      </td>
                    </template>
                    <td>
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ d.salary_value | parse('number2fraction') }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ d.pembulatan_salary | parse('number2fraction') }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ d.selisih_pembulatan | parse('number2fraction') }}</span>
                      </div>
                    </td>
                  </tr>
                </template>
              </tbody>
              <tfoot>
                <tr>
                  <th class="nowrap-table"></th>
                  <th class="text-right sticky-left">
