                      <span class="text-right">Total</span>
                    </div>
                  </th>
                  <th class="" colspan="3"></th>
                  <template v-for="(h,i) in mainData[activeTab][0].data_salary">
                    <th v-for="(s,j) in h.data_salary_components" :key="j+'-'+i+'-footer-total'" class="">
                      <div class="d-flex justify-content-between">
                        <span class="text-capitalize">Rp.</span>
                        <span class="text-right font-weight-bolder">{{ mainData[activeTab].map(a => a.data_salary[i].data_salary_components[j].value).filter(b => b).reduce((acc, curr) => acc + curr, 0) | parse('number2fraction') }}</span>
                      </div>
                    </th>
                  </template>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].map(a => a.salary_value).reduce((acc, curr) => acc + curr, 0) | parse('number2fraction') }}</span>
                    </div>
                  </th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].map(a => a.pembulatan_salary).reduce((acc, curr) => acc + curr, 0) | parse('number2fraction') }}</span>
                    </div>
                  </th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].map(a => a.selisih_pembulatan).reduce((acc, curr) => acc + curr, 0) | parse('number2fraction') }}</span>
                    </div>
                  </th>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
        <div v-else-if="activeTab == 'jurnal'" class="card-body pt-0">
          <div v-if="mainData[activeTab].length" class="scrolling-table">
            <table ref="tableExport" class="table v-center header-center header-uppercase control-sheet-table table-custom-header table-hover mb-0">
              <thead class="bg-light-primary">
                <tr>
                  <th rowspan="2" class="nowrap-table">No</th>
                  <th rowspan="2" class="min-w-250px sticky-left">Kategori Jabatan</th>
                  <th rowspan="2" class="">Status Ketenagakerjaan</th>
                  <template v-for="(h,i) in mainData[activeTab][0].data_salary">
                    <th v-if="h.data_salary_components.length>1" :key="i+'-header-'+activeTab" :colspan="h.data_salary_components.length">{{ h.name }}</th>
                    <th v-else :key="i+'-header-'+activeTab" :rowspan="2">{{ h.name }}</th>
                  </template>
                </tr>
                <tr>
                  <template v-for="(h,i) in mainData[activeTab][0].data_salary.filter(x=> x.data_salary_components.length>1)">
                    <th v-for="(s,j) in h.data_salary_components" :key="j+'-'+i+'-header-'+activeTab" style="top:28.59px">{{ s.name }}</th>
                  </template>
                </tr>
              </thead>
              <tbody>
                <template v-for="(d,i) in mainData[activeTab]">
                  <tr :key="i+'-data-'+activeTab">
                    <td class="nowrap-table text-right">{{ i+1 }}.</td>
                    <td class="sticky-left font-weight-bolder text-primary bg-hover-light-primary" :class="fullwidthColumns.includes('rel_job_position_category_id') ? 'nowrap-table': 'max-w-170px'">
                      <div class="text-line-1"> {{ d.rel_job_position_category_id }} </div>
                    </td>
                    <td @click="setFullWidth('rel_status_employment_id')" class="pointer" :class="fullwidthColumns.includes('rel_status_employment_id') ? 'nowrap-table': ''">
                      <div class="text-line-1"> {{ d.rel_status_employment_id }} </div>
                    </td>
                    <template v-for="(h,j) in d.data_salary">
                      <td v-for="(s,k) in h.data_salary_components" :key="k+'-'+j+'-'+i+'-body-'+activeTab" class="">
                        <div v-if="s.value" class="d-flex justify-content-between">
                          <span class="">Rp.</span>
                          <span class="text-right">{{ s.value | parse('number2fraction') }}</span>
                        </div>
                        <span v-else class="d-block text-right">-</span>
                      </td>
                    </template>
                  </tr>
                </template>
