              <thead class="bg-light-primary">
                <tr>
                  <th class="nowrap-table">Kode Akun</th>
                  <th class="">Nama Akun</th>
                  <th class="nowrap-table">DEBET</th>
                  <th class="nowrap-table">KREDIT</th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(d,i) in mainData[activeTab].data">
                  <tr :key="i+'-data-'+activeTab">
                    <td class="nowrap-table text-left">{{ d.account_code }}</td>
                    <td class="font-weight-bolder">
                      <div class="text-line-1"> {{ d.account_name }} </div>
                    </td>
                    <td>
                      <div v-if="d.debit_kredit === 'debit'" class="d-flex justify-content-between">
                        <span class="text-capitalize">Rp.</span>
                        <span class="text-right font-weight-bolder">{{ d.sum_value | parse('number2fraction') }}</span>
                      </div>
                    </td>
                    <td>
                      <div v-if="d.debit_kredit === 'kredit'" class="d-flex justify-content-between">
                        <span class="text-capitalize">Rp.</span>
                        <span class="text-right font-weight-bolder">{{ d.sum_value | parse('number2fraction') }}</span>
                      </div>
                    </td>
                  </tr>
                </template>
              </tbody>
              <tfoot>
                <tr>
                  <th colspan="2" class="text-right">Jumlah</th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].summary_data.total_debit_value | parse('number2fraction') }}</span>
                    </div>
                  </th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].summary_data.total_kredit_value | parse('number2fraction') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <th colspan="2" class="text-right">TOTAL (DEBET - KREDIT)</th>
