                      <span class="text-capitalize">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ mainData[activeTab].summary_data.total_value | parse('number2fraction') }}</span>
                    </div>
                  </th>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
        <div v-if="verifyList && mainData.checklist" class="card-body pt-0">
          <div class="row align-items-center">
            <div class="col-md-12 col-lg-4">
              <div class="sumarry-wrapper">
                <span class="font-weight-bolder d-block">Perhitungan Gaji Tenaga Kerja periode {{ selectedPeriod | parse('period') }} </span>
                <template v-if="mainData.checklist && mainData.checklist.status_overall">
                  <hr>
