                    <tr>
                      <td class="pt-0 pl-0 nowrap-table">Total Penerimaan</td>
                      <td class="pt-0 pr-0">
                        <div class="d-flex justify-content-between">
                          <span class="pr-1">: Rp.</span>
                          <span class="text-right font-weight-bold">{{ verifyList.data_summary.sum_salary_value | parse('number2fraction') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="pt-0 pl-0 nowrap-table">Pembulatan</td>
                      <td class="pt-0 pr-0">
                        <div class="d-flex justify-content-between">
                          <span class="pr-1">: Rp.</span>
                          <span class="text-right font-weight-bold">{{ verifyList.data_summary.sum_pembulatan_salary | parse('number2fraction') }}</span>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td class="pt-0 pl-0 nowrap-table">Selisih</td>
                      <td class="pt-0 pr-0">
                        <div class="d-flex justify-content-between">
                          <span class="pr-1">: Rp.</span>
                          <span class="text-right font-weight-bold">{{ verifyList.data_summary.sum_selisih_pembulatan | parse('number2fraction') }}</span>
                        </div>
                      </td>
                    </tr>
                  </table>
                  <template v-if="verifyList.data_summary.is_lock || verifyList.data_summary.status_code === 'revision_request'">
