                    <div class="d-flex justify-content-between align-items-center">
                      <span class="font-size-sm font-weight-bold">Deskripsi / Keterangan</span>
                      <span :class="verifyList.data_summary.status_code === 'revision_request'? ' label-light-danger' : ' label-light-primary'" class="label label-lg label-inline font-weight-bold py-4">{{ verifyList.data_summary.status_code | parse('verify') }}</span>
                    </div>
                    <p class="mb-0"> {{ verifyList.data_summary.description || 'Belum ada deskripsi / keterangan verifikasi' }}</p>
                  </template>
                  <hr>
                  <template v-if="!verifyList.data_summary.is_lock">
                    <div class="d-flex mt-3">
                      <button :disabled="isMainLoading" @click="calculateAllEmployee()" class="btn btn-primary mr-3 flex-fill">
                        <i class="ri-calculator-line"></i> Hitung Gaji semua Tenaga Kerja</button>
                      <button @click="lockDocument(verifyList.data_summary)" class="btn btn-icon btn-success">
                        <i class="ri-lock-line"></i>
                      </button>
                    </div>
                    <p class="mb-0 mt-2 font-size-sm font-weight-bold">Kunci Perhitungan Gaji untuk mengajukan verifikasi perhitungan.</p>
                  </template>
                  <template v-else>
                    <div class="d-flex mt-3">
                      <button :disabled="verifyList.data_summary.status_code != 'approved'" class="btn btn-info mr-3 flex-fill">
                        <i class="ri-secure-payment-line"></i> Terbitkan Faktur Keuangan (FINA)</button>
                      <button @click="lockDocument(verifyList.data_summary)" class="btn btn-icon btn-danger">
                        <i class="ri-lock-unlock-line"></i>
                      </button>
                    </div>
                    <button :disabled="verifyList.data_summary.status_code != 'approved'" @click="$_sys.xlsHTML($refs.tableExport, labelPrint+' - '+labelPeriod, labelPeriod)" class="btn btn-lg btn-success mb-3 mt-2 btn-block">
                      <i class="ri-file-excel-2-line"></i> Export {{ labelPrint }}
                    </button>
                    <p v-if="verifyList.data_summary.status_code != 'approved'" class="mb-0 mt-2 font-size-sm font-weight-bold">Faktur dapat diterbitkan ketika data verifikasi sudah lengkap. </p>
                  </template>
                </template>
                <template v-else>
                  <div class="alert alert-danger mt-3 mb-0 p-5" role="alert">
                    <h4 class="alert-heading">ERROR !</h4>
                    <p>Silahkan selesaikan seluruh konfigurasi Modul untuk melanjutkan ke Perhitungan Gaji Tenaga Kerja</p>
                    <div class="border-bottom border-white opacity-20 mb-5"></div>
                    <p class="mb-0">{{ mainData.checklist.data.filter(x=> x.value !== 'success' ).length }} dari {{ mainData.checklist.data.length }} Modul belum selesai.</p>
                  </div>
                </template>
              </div>
            </div>
            <div v-if="verifyList.data_summary.is_lock" class="col-md-12 col-lg-8">
              <div v-if="verifyList.data.length" class="d-flex justify-content-around align-items-center">
                <div v-for="(s,i) in verifyList.data" :key="i+'-signature'" class="signature text-center">
                  <span class="d-block">Diverifikasi oleh,</span>
                  <div class="d-flex align-items-center justify-content-center text-center my-5 min-h-80px">
                    <template v-if="!s.verified_by">
                      <button v-if="isAllowedVerify(s)" @click="approveSheet(true, s)" class="btn btn-sm btn-success">Approve</button>
                      <button v-if="isAllowedVerify(s)" @click="approveSheet(false, s)" class="btn btn-sm btn-outline-danger ml-2">Revisi</button>
                      <img v-else src="/static/img/default/unverified-150X150.png" alt="unverified" class="img-unverified">
                    </template>
                    <vue-qr v-else :text="generateText(s)" :size="80" :margin="5"></vue-qr>
                  </div>
                  <div class="text-center">
                    <span v-if="s.verified_by" class="d-block font-weight-bold">{{ s.rel_verified_by }}</span>
                    <span v-else class="d-block font-weight-bold font-italic">Belum Diverifikasi</span>
                    <hr class="text-dark-75 my-1">
                    <span class="d-block">{{ s.rel_privillege_role_id }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-overlay>
    </div>
    <b-modal v-if="modalData" id="modal-payroll-details" title="Rincian &amp; Slip Gaji" hide-footer hide-header size="xl">
      <div class="inner-tab">
        <div class="row">
          <div class="col-lg-6">
            <b-overlay :show="isLoadingSlip" rounded="sm">
              <div id="printSlip" class="border-primary p-5 bg-white" style="border: 2px solid;border-radius:13px">
                <div class="d-flex justify-content-start align-items-center">
                  <img :src="$_config.logo.lg" alt="Logo KIW" class="max-h-45px" />
                  <div class="text-left d-flex flex-column ml-5">
                    <h6 class="mb-0 font-weight-bolder"> {{ $_config.company }}</h6>
                    <span class="font-size-sm font-weight-bolder">Slip Gaji Periode {{ modalData.periode_month | parse('period') }}</span>
                  </div>
                </div>
                <hr>
                <table class="table no-border">
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Periode / Bulan</td>
                    <td class="nowrap-table py-0">:</td>
                    <td class="py-0 text-left"> {{ modalData.periode_month | parse('period') }} </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Nama Lengkap</td>
                    <td class="nowrap-table py-0">:</td>
                    <td class="py-0 text-left"> {{ modalData.employee.rel_employee_id }} </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Jabatan</td>
                    <td class="nowrap-table py-0">:</td>
                    <td class="py-0 text-left"> {{ modalData.employee.rel_job_position_id }} </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Status Ketenagakerjaan</td>
                    <td class="nowrap-table py-0">:</td>
                    <td class="py-0 text-left"> {{ modalData.employee.rel_status_employment_id }} </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Hari Kerja</td>
                    <td class="nowrap-table py-0">:</td>
                    <td v-if="modalData.presence" class="py-0 text-left"> {{ modalData.presence.work_day_count || 0 }} Hari</td>
                    <td v-else class="py-0 text-left"> Tidak ditemukan! </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Tidak Masuk</td>
                    <td class="nowrap-table py-0">:</td>
                    <td v-if="modalData.presence" class="py-0 text-left">{{ modalData.presence.work_day_lost_count || 0 }} Hari</td>
                    <td v-else class="py-0 text-left"> Tidak ditemukan! </td>
                  </tr>
                  <tr>
                    <td class="nowrap-table py-0 pl-0">Terlambat</td>
                    <td class="nowrap-table py-0">:</td>
                    <td v-if="modalData.presence" class="py-0 text-left"> {{ modalData.presence.work_day_late_count || 0 }} Hari, {{ modalData.presence.work_day_minute_late_count || 0 }} Menit </td>
                    <td v-else class="py-0 text-left"> Tidak ditemukan! </td>
                  </tr>
                </table>
                <hr>
                <table class="table no-border mb-0">
                  <template v-for="(p,i) in modalData.listTree">
                    <template v-if="p.children.filter(x => x.charge === 'employee' ).length">
                      <tr :key="i+'-parent-list'">
                        <th class="py-2" style="border:none" :colspan="4">{{ p.name }}</th>
                      </tr>
                      <tr v-for="(c,j) in p.children.filter(x => x.charge === 'employee' )" :key="i+'-'+j+'-child-list'">
                        <td class="py-1" style="border:none">&emsp;</td>
                        <td class="py-1" style="border:none">{{ c.rel_salary_component_id }} </td>
                        <td class="py-1" style="border:none">
                          <div class="d-flex justify-content-between">
                            <span class="">Rp.</span>
                            <span class="text-right font-weight-bold">{{ c.salary_component_value || 0 | parse('number') }}</span>
                          </div>
                        </td>
                      </tr>
                      <tr :key="i+'-child-list-spacing'">
                        <td class="py-1" style="border:none" :colspan="4"></td>
                      </tr>
                    </template>
                  </template>
                  <tr>
                    <th class="py-2 text-uppercase" style="border:none" :colspan="4">Total Gaji</th>
                  </tr>
                  <tr>
                    <td class="py-1" style="border:none">&emsp;</td>
                    <td class="py-1" style="border:none">Penerimaan</td>
                    <td class="py-1" style="border:none">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.employee.salary_value || 0 | parse('number') }}</span>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="py-1" style="border:none">&emsp;</td>
                    <td class="py-1" style="border:none">Pembulatan</td>
                    <td class="py-1" style="border:none">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.employee.pembulatan_salary || 0 | parse('number') }}</span>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="py-1" style="border:none">&emsp;</td>
                    <td class="py-1" style="border:none">Selisih Pembulatan</td>
                    <td class="py-1" style="border:none">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.employee.selisih_pembulatan || 0 | parse('number') }}</span>
                      </div>
                    </td>
                  </tr>
                </table>
                <div class=" text-right mt-5">
                  <span class="font-size-sm font-italic">Dicetak oleh {{ $store.state.profile.fullname }}<br>{{ new Date() | parse('longDateTime') }}</span>
                </div>
              </div>
            </b-overlay>
          </div>
          <div class="col-lg-6">
            <table class="table no-border mb-10 mt-5 mt-lg-0">
              <tr>
                <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-0">Tenaga Kerja</th>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Nama Lengkap</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> {{ modalData.employee.rel_employee_id }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Jabatan</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> {{ modalData.employee.rel_job_position_id }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Status Ketenagakerjaan</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> {{ modalData.employee.rel_status_employment_id }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">MPP</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> {{ modalData.employee.is_mpp | parse('ynBool') }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">PLT</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> {{ modalData.employee.is_plt | parse('ynBool') }} </td>
              </tr>
              <tr>
                <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Presensi Kehadiran</th>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Hari Kerja</td>
                <td class="nowrap-table py-1">:</td>
                <td v-if="modalData.presence" class="py-1 text-left"> {{ modalData.presence.work_day_count || 0 }} Hari</td>
                <td v-else class="py-1 text-left"> Tidak ditemukan! </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Tidak Masuk</td>
                <td class="nowrap-table py-1">:</td>
                <td v-if="modalData.presence" class="py-1 text-left"> {{ modalData.presence.work_day_lost_count || 0 }} Hari</td>
                <td v-else class="py-1 text-left"> Tidak ditemukan! </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Terlambat</td>
                <td class="nowrap-table py-1">:</td>
                <td v-if="modalData.presence" class="py-1 text-left"> {{ modalData.presence.work_day_late_count || 0 }} Hari, {{ modalData.presence.work_day_minute_late_count || 0 }} Menit </td>
                <td v-else class="py-1 text-left"> Tidak ditemukan! </td>
              </tr>
              <template v-if="modalData.listTree.filter(x => x.children.filter(y=> y.charge === 'companies').length).length">
                <tr>
                  <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Beban Perusahaan</th>
                </tr>
                <template v-for="(p,i) in modalData.listTree">
                  <tr v-for="(b,j) in p.children.filter(x => x.charge === 'companies' )" :key="i+'-'+j+'-beban-perusahaan'">
                    <td class="nowrap-table py-1 pl-0">{{ b.rel_salary_component_id }}</td>
                    <td class="nowrap-table py-1">:</td>
                    <td class="py-1 text-left"> Rp.&emsp;{{ b.salary_component_value || 0 | parse('number') }}</td>
                  </tr>
                </template>
              </template>
              <tr>
                <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Penggajian</th>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Status Penggajian</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left font-weight-bolder" :class="modalData.employee.status_salary === 'calculated'? 'text-success': 'text-danger' "> {{ modalData.status_salary | parse('calculated') }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Total Penerimaan</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.salary_value || 0 | parse('number') }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Pembulatan</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.pembulatan_salary || 0 | parse('number') }} </td>
              </tr>
              <tr>
                <td class="nowrap-table py-1 pl-0">Selisih Pembulatan</td>
                <td class="nowrap-table py-1">:</td>
                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.selisih_pembulatan || 0 | parse('number') }} </td>
              </tr>
              <tr v-if="modalData.employee.salary_calculated_by && modalData.employee.salary_calculated_at">
                <td colspan="3" class="font-size-sm font-italic text-right">Dihitung Oleh {{ modalData.employee.rel_salary_calculated_by }} pada {{ modalData.employee.salary_calculated_at | parse('longDateTime') }}</td>
              </tr>
            </table>
            <div v-if="mainData.checklist.status_overall" class="btn-actions">
              <button @click="recountDetail()" class="btn btn-light-primary"><i class="ri-exchange-funds-line"></i> Hitung Ulang</button>
              <button v-print="printConfig" class="btn btn-light-danger"><i class="ri-printer-line"></i> Cetak PDF</button>
              <button @click="downloadSlip(selectedPeriod)" class="btn btn-light-success"><i class="ri-download-cloud-line"></i> Download</button>
            </div>
          </div>
        </div>
      </div>
    </b-modal>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'control-sheet-payroll',
  data () {
    return {
      activeTab: 'sheet',
      selectedPeriod: null,
      isMainLoading: false,
      isLoadingSlip: false,
      config: {
        permission: {
          create: 'create-control-sheet-payroll',
          read: 'view-control-sheet-payroll',
          update: 'update-control-sheet-payroll',
          delete: 'delete-control-sheet-payroll'
        }
      },
      mainData: {
        sheet: [],
        jurnal: [],
        fina: null,
        checklist: null
      },
      modalData: null,
      printConfig: {
        id: 'printSlip',
        preview: false,
        standard: 'strict'
      },
      fullwidthColumns: [],
      verifyList: null
    }
  },
  watch: {
    activeTab: {
      deep: false,
      immediate: false,
      async handler (e) {
        let endpoint = null
        let filter = null
        if (e === 'sheet') {
          endpoint = 'custom/control-sheet-payroll/list'
          filter = {
            periode_month: this.selectedPeriod,
            sort_by: 'fullname',
            sort: 'ASC'
          }
        } else if (e === 'jurnal') {
          endpoint = 'custom/control-sheet-jurnal/list'
          filter = {
            periode_month: this.selectedPeriod
          }
        } else {
          endpoint = 'custom/payroll-journals/list'
          filter = {
            periode_month: this.selectedPeriod
          }
        }
        if (this.selectedPeriod) {
          this.isMainLoading = true
          this.$set(this.mainData, e, await this.sendAPI('get', endpoint, filter, e !== 'fina'))
          this.isMainLoading = false
        }
      }
    },
    selectedPeriod: {
      deep: false,
      immediate: true,
      async handler (to) {
        let endpoint = null
        let filter = null
        if (this.activeTab === 'sheet') {
          endpoint = 'custom/control-sheet-payroll/list'
          filter = {
            periode_month: to,
            sort_by: 'fullname',
            sort: 'ASC'
          }
        } else if (this.activeTab === 'jurnal') {
          endpoint = 'custom/control-sheet-jurnal/list'
          filter = {
            periode_month: to
          }
        } else {
          endpoint = 'custom/payroll-journals/list'
          filter = {
            periode_month: this.selectedPeriod
          }
        }
        if (to) {
          this.$store.commit('set', ['lastSelectedPeriodPayroll', to, true])
          this.isMainLoading = true
          this.$set(this.mainData, 'checklist', await this.sendAPI('get', 'custom/payroll-periode-calculation/checklist', { periode_month: to }, false))
          this.$set(this.mainData, this.activeTab, await this.sendAPI('get', endpoint, filter, this.activeTab !== 'fina'))
          this.getVerifyList(to)
          this.isMainLoading = false
        }
      }
    }
  },
  computed: {
    labelPrint () {
      if (this.activeTab === 'sheet') return 'Control Sheet (Perorangan)'
      else if (this.activeTab === 'jurnal') return 'Control Sheet (Jabatan)'
      else return 'Faktur Keuangan'
    },
    labelPeriod () {
      return moment(this.selectedPeriod).format('MMMM YYYY')
    }
  },
  beforeMount () {
    this.setThisMonth()
  },
  mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
  },
  methods: {
    // base64ToBlob (data, mime) {
    //   let base64 = window.btoa(window.unescape(encodeURIComponent(data)))
    //   let bstr = atob(base64)
    //   let n = bstr.length
    //   let u8arr = new Uint8ClampedArray(n)
    //   while (n--) {
    //     u8arr[n] = bstr.charCodeAt(n)
    //   }
    //   return new Blob([u8arr], {type: mime})
    // },
    // async getExportData () {
    //   // eslint-disable-next-line no-template-curly-in-string
    //   let xlsTemp = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><meta name=ProgId content=Excel.Sheet> <meta name=Generator content="Microsoft Excel 11"><meta http-equiv="Content-Type" content="text/html; charset=UTF-8"><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>${worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--><style>br {mso-data-placement: same-cell;}</style></head><body><table>${table}</table></body></html>'
    //   const xlsData = this.$refs.tableExport.innerHTML
    //   // eslint-disable-next-line no-template-curly-in-string
    //   const data = xlsTemp.replace('${table}', xlsData).replace('${worksheet}', 'hehe')
    //   let blob = this.base64ToBlob(data, 'application/vnd.ms-excel')
    //   download(blob, 'huhu.xls', 'application/vnd.ms-excel')
    // },
    setFullWidth (field) {
      if (this.fullwidthColumns.includes(field)) {
        this.fullwidthColumns = this.fullwidthColumns.filter(x => x !== field)
      } else this.fullwidthColumns.push(field)
    },
    sendAPI (method, api, param, returnData = true) {
      return this.$_api[method](api, param).then(res => {
        return returnData ? res.data : res
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    async setThisMonth () {
      const alreadySelect = this.$store.state.lastSelectedPeriodPayroll
      if (!alreadySelect) {
        let data = await this.sendAPI('list', 'payroll-periodes', { periode_month: moment(new Date()).set({ 'date': 1 }).format('YYYY-MM-DD') })
        if (data.length) {
          this.selectedPeriod = data[0].periode_month
          this.$store.commit('set', ['lastSelectedPeriodPayroll', data[0].periode_month, false])
        }
      } else this.selectedPeriod = alreadySelect
    },
    async getVerifyList (to) {
      this.verifyList = await this.sendAPI('get', 'custom/control-sheet-payroll/summary-verifications', {
        periode_month: to,
        sort_by: 'verification_order',
        sort: 'ASC'
      }, false)
    },
    isAllowedVerify (data) {
      const profile = JSON.parse(localStorage.getItem('profile'))
      if (profile.role_id === -1) return true
      else {
        return profile.role_id === data.privillege_role_id
      }
    },
    generateText (data) {
      let text = 'Telah diverifikasi oleh, ' + data.rel_verified_by + ' - ' + data.rel_job_position_id
      text += '\nSebagai ' + data.rel_privillege_role_id
      text += '\npada ' + moment(data.verified_by).format('LLLL')
      text += '\n\nvia ' + window.location.origin
      return text
    },

    async getPayrollDetail (id, data) {
      const listTree = await this.sendAPI('get', 'custom/payroll-periode-calculation/salary-component-on-periodes-list-tree', { id: data.id }, false)
      const detail = {
        listTree: listTree.data.data_salary_detail,
        presence: listTree.data.data_employee_presence_recaps,
        employee: listTree.data.data_employee
      }
      this.modalData = Object.assign({}, detail, data)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    async downloadSlip (e) {
      this.isLoadingSlip = true
      var domtoimage = require('dom-to-image-more')
      const capture = document.getElementById('printSlip')
      await domtoimage.toPng(capture).then(function (url) {
        const link = document.createElement('a')
        link.href = url
        const filename = 'Slip Gaji Periode ' + moment(e).format('MMMM YYYY')
        link.setAttribute('download', filename + '.png') // or any other extension
        document.body.appendChild(link)
        link.click()
      })
      this.isLoadingSlip = false
    },
    async calculateAllEmployee () {
      this.isMainLoading = true
      const result = await this.sendAPI('post', 'custom/payroll-periode-calculation/salary-calculation-all', { periode_month: this.selectedPeriod }, false)
      if (result && result.check) {
        this.countFinaJournal()
        this.reloadTable()
        this.$_alert.success('Berhasil menghitung semua Gaji Tenaga Kerja')
      }
      this.isMainLoading = false
    },
    async recountDetail () {
      this.isLoadingSlip = true
      let formData = {
        id: this.modalData.id,
        periode_month: this.selectedPeriod
      }
      const result = await this.sendAPI('post', 'custom/payroll-periode-calculation/salary-calculation-single', formData, false)
      if (result && result.check) {
        const listTree = await this.sendAPI('get', 'custom/payroll-periode-calculation/salary-component-on-periodes-list-tree', { id: this.modalData.id }, false)
        const detail = {
          listTree: listTree.data.data_salary_detail,
          presence: listTree.data.data_employee_presence_recaps,
          employee: listTree.data.data_employee
        }
        this.modalData = Object.assign(this.modalData, detail)
        this.countFinaJournal()
        this.reloadTable()
      }
      this.isLoadingSlip = false
    },
    async countFinaJournal () {
      let formData = {
        periode_month: this.selectedPeriod
      }
      const result = await this.sendAPI('post', 'custom/payroll-journals/do-calculation', formData, false)
      if (result && result.check) {
      } else this.$_alert.error('Journals Payroll calculation Error!')
    },
    async reloadTable () {
      let endpoint = null
      let filter = null
      if (this.activeTab === 'sheet') {
        endpoint = 'custom/control-sheet-payroll/list'
        filter = {
          periode_month: this.selectedPeriod,
          sort_by: 'fullname',
          sort: 'ASC'
        }
      } else if (this.activeTab === 'jurnal') {
        endpoint = 'custom/control-sheet-jurnal/list'
        filter = {
          periode_month: this.selectedPeriod
        }
      } else {
        endpoint = 'custom/payroll-journals/list'
        filter = {
          periode_month: this.selectedPeriod
        }
      }
      this.isMainLoading = true
      this.getVerifyList(this.selectedPeriod)
      this.$set(this.mainData, this.activeTab, await this.sendAPI('get', endpoint, filter, this.activeTab !== 'fina'))
      this.isMainLoading = false
    },
    lockDocument (data) {
      const form = {
        periode_month: this.selectedPeriod
      }
      if (data.is_lock === false) {
        this.$swal({
          title: 'Konfirmasi Kunci Perhitungan',
          html: `
          <div class="alert alert-custom alert-outline-danger mt-3 p-3" role = "alert">
              <div class="alert-icon">
                <span class="svg-icon svg-icon-danger svg-icon-xl">
                  <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                          <rect x="0" y="0" width="24" height="24"/>
                          <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3"/>
                          <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1"/>
                          <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1"/>
                      </g>
                  </svg>
                </span>
              </div>
              <div class="alert-text font-size-sm">Hal yang tidak terduga akan terjadi jika Anda tidak membaca ini!</div>
          </div>
          <p class="my-6"> Tindakan ini tidak bisa dibatalkan. Perhitungan Gaji <b>tidak dapat diperbarui</b> saat perhitungan dikunci</p>
          <span class="text-dark-75">Silahkan ketik <span class="text-dark-75 font-weight-bolder">"konfirmasi"</span> untuk mengunci perhitungan</span>
          `,
          input: 'text',
          icon: 'warning',
          iconColor: '#d42b21',
          padding: '.75rem',
          width: '37rem',
          focusConfirm: false,
          showCancelButton: false,
          confirmButtonText: 'Saya paham konsekuensinya, lanjutkan Kunci Perhitungan',
          customClass: {
            confirmButton: 'btn btn-light-danger',
            actions: 'mt-0 mb-5',
            htmlContainer: 'text-left overflow-hidden'
          },
          didOpen: () => {
            this.$swal.disableButtons()
            this.$swal.getInput().addEventListener('keyup', (e) => {
              if (e.target.value === 'konfirmasi') {
                this.$swal.enableButtons()
              } else {
                this.$swal.disableButtons()
              }
            })
          }
        }).then((result) => {
          if (result.isConfirmed) {
            this.$_api.post('custom/control-sheet-payroll/submit-lock', form).then(res => {
              this.$_alert.success('Berhasil mengunci Perhitungan')
              this.getVerifyList(this.selectedPeriod)
            })
          }
        })
      } else {
        this.$swal({
          title: 'Buka Kunci Perhitungan?',
          html: `
          <div class="alert alert-custom alert-outline-danger mt-3 p-3" role = "alert">
              <div class="alert-icon">
                <span class="svg-icon svg-icon-danger svg-icon-xl">
                  <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                          <rect x="0" y="0" width="24" height="24"/>
                          <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3"/>
                          <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1"/>
                          <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1"/>
                      </g>
                  </svg>
                </span>
              </div>
              <div class="alert-text font-size-sm">Hal yang tidak terduga akan terjadi jika Anda tidak membaca ini!</div>
          </div>
          <p class="my-6"> Tindakan ini tidak bisa dibatalkan. Ini akan <b>menghapus secara permanen</b> semua verifikasi yang telah dilakukan pada dokumen ini.</p>
          <span class="text-dark-75">Silahkan ketik <span class="text-dark-75 font-weight-bolder">"konfirmasi"</span> untuk membuka dokumen ini</span>
          `,
          input: 'text',
          icon: 'warning',
          iconColor: '#d42b21',
          padding: '.75rem',
          width: '37rem',
          focusConfirm: false,
          showCancelButton: false,
          confirmButtonText: 'Saya paham konsekuensinya, Buka dokumen ini',
          customClass: {
            confirmButton: 'btn btn-light-danger',
            actions: 'mt-0 mb-5',
            htmlContainer: 'text-left overflow-hidden'
          },
          didOpen: () => {
            this.$swal.disableButtons()
            this.$swal.getInput().addEventListener('keyup', (e) => {
              if (e.target.value === 'konfirmasi') {
                this.$swal.enableButtons()
              } else {
                this.$swal.disableButtons()
              }
            })
          }
        }).then((result) => {
          if (result.isConfirmed) {
            this.$_api.post('custom/control-sheet-payroll/submit-unlock', form).then(res => {
              this.$_alert.success('Berhasil membuka kunci perhitungan')
              this.getVerifyList(this.selectedPeriod)
            })
          }
        })
      }
    },
    approveSheet (e = true, s) {
      this.$swal({
        title: e ? 'Approve Control Sheet ?' : 'Permintaan Revisi',
        html: `<span class="text-dark-75">Keterangan&nbsp;:</span>`,
        input: 'textarea',
        icon: e ? 'success' : 'warning',
        iconColor: e ? '#3AAC12' : '#d42b21',
        padding: '.75rem',
        width: '37rem',
        focusConfirm: false,
        showCancelButton: false,
        confirmButtonText: e ? 'Approve Control Sheet' : 'Kirim Permintaan Revisi',
        customClass: {
          confirmButton: e ? 'btn btn-success' : 'btn btn-light-danger',
          actions: 'mt-0 mb-5',
          htmlContainer: 'text-left overflow-hidden'
        },
        didOpen: () => {
          this.$swal.disableButtons()
          this.$swal.getInput().addEventListener('keyup', (e) => {
            if (e.target.value) {
              this.$swal.enableButtons()
            } else {
              this.$swal.disableButtons()
            }
          })
        }
      }).then((result) => {
        if (result.isConfirmed) {
          const form = {
            'id': s.id,
            'payroll_periode_id': s.payroll_periode_id,
            'privillege_role_id': s.privillege_role_id,
            'verification_order': s.verification_order,
            'status_code': e ? 'approved' : 'revision_request',
            'description': this.$swal.getInput().value
          }
          this.$_api.post('custom/control-sheet-payroll/do-verifications', form).then(res => {
            this.$_alert.success(e ? 'Berhasil memverifikasi data' : 'Berhasil mengirim permintaan Revisi')
            this.getVerifyList(this.selectedPeriod)
          })
        }
      })
    }
  }
}
</script>

<style>
  .swal2-textarea {
    margin: 0.5em auto;
  }
  .w-fit-content {
    width: 100%;
  }
  .table.control-sheet-table th,
  .table.control-sheet-table td {
    width: 170px;
    min-width: 170px;
    padding: 0.35rem 0.75rem;
    border: 0 !important;
    box-shadow: inset 0 0 0 0.15px rgb(0 0 0 / 30%);
  }
  .sticky-left {
    position: sticky !important;
    left: -1px !important;
    background-color: #ffffff;
    z-index: 2;
    max-width: 250px;
    min-width: 170px;
  }
  .scrolling-table {
    display: block;
    width: 100%;
    max-height: 75vh;
    overflow-y: auto;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  .scrolling-table thead th {
    position: sticky !important;
    top: 0;
    background-color: #ffffff;
    z-index: 3;
  }
  .scrolling-table tfoot th {
    position: sticky !important;
    bottom: 0;
    background-color: #ffffff;
    z-index: 3;
  }
  .scrolling-table tfoot th.sticky-left,
  .scrolling-table thead th.sticky-left {
    z-index: 4;
  }
  .img-unverified {
    width: 80px;
    height: 80px;
    object-fit: cover;
  }
  .sumarry-wrapper {
    border: 2px solid var(--primary-color);
    border-radius: 8px;
    padding: 1.5rem;
  }
</style>



